
.guide {
  position: absolute;
  left: 115%;
  right: 115%;
  text-align: center;
  line-height: 1;
  white-space: nowrap;
}
.guide.right {
  right: auto;
  bottom: 5%;
}
.guide.left {
  left: auto;
  bottom: 5%;
}
.guide p {
  text-align: left;
  margin-bottom: 350px;
  opacity: .5;
}
.guide p iframe {
  margin-top: 20px;
}
.guide a {
  color: #005850;
  font-size: 30px;
  position: relative;
  z-index: 1;
  cursor: alias;
  text-decoration: none;
}
.guide.qr {
  left: auto;
  top: 5%;
  width: 190px;
  height: 190px;
  opacity: .45;
  padding: 0 0 40px 40px;
  text-align: right;
}
.guide.qr:hover img {
  width: 100%;
  height: 100%;
}
.guide.qr img {
  width: 38px;
  height: 38px;
}
.guide > div {
  width: 100px;
  height: 54px;
  background: #364d4b;
  display: inline-block;
  border-radius: 4px;
  position: relative;
  color: #acc3c1;
  font-size: 16px;
}
.guide > div em {
  display: block;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: transparent transparent /*#ccc*/ #acc3c1;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -9px 0 0 -6px;
}
.guide > div:before,
.guide > div:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 4px;
  box-shadow: 0 5px 10px rgba(255, 255, 255, 0.15) inset;
}
.guide > div:before {
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.15) inset;
}
.guide .up {
  height: 60px;
  display: block;
  margin: 0 auto 2px;
}
.guide .down {
  margin: 0 10px;
}
.guide .space {
  left: auto;
  bottom: 5%;
  height: 80px;
  width: 400px;
  line-height: 80px;
  letter-spacing: 2px;
}



