
.logo {
  width: 224px;
  height: 200px;
  position: absolute;
  top: 100px;
  left: 12px;
  text-align: center;
  overflow: hidden;
}
.logo p {
  position: absolute;
  width: 100%;
  line-height: 1.4;
  top: 100px;
  left: 0;
  font-family: initial;
  letter-spacing: 6px;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.35);
}
.logo .dragon {
  width: 80px;
  height: 86px;
  margin: 0 auto;
  background-position: 0 -100px;
}
.logo .dragon.r1,
.logo .dragon.l1 {
  background-position: 0 -100px;
}
.logo .dragon.r2,
.logo .dragon.l2 {
  background-position: -100px -100px;
}
.logo .dragon.r3,
.logo .dragon.l3 {
  background-position: -200px -100px;
}
.logo .dragon.r4,
.logo .dragon.l4 {
  background-position: -300px -100px;
}
.logo .dragon.l1,
.logo .dragon.l2,
.logo .dragon.l3,
.logo .dragon.l4 {
  transform: scale(-1, 1);
  -webkit-transform: scale(-1, 1);
  -ms-transform: scale(-1, 1);
  -moz-transform: scale(-1, 1);
  -o-transform: scale(-1, 1);
}



