
.decorate h1 {
  position: absolute;
  width: 100%;
  text-align: center;
  font-weight: normal;
  top: -12px;
  left: 0;
  margin: 0;
  padding: 0;
  font-size: 30px;
}
.decorate .topBorder {
  height: 10px;
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  overflow: hidden;
}
.decorate .topBorder span {
  display: block;
  width: 10px;
  height: 10px;
  overflow: hidden;
  background: #000;
}
.decorate .topBorder span.mr {
  margin-right: 10px;
}
.decorate .topBorder span.ml {
  margin-left: 10px;
}
.decorate .view {
  position: absolute;
  right: -70px;
  top: 20px;
  width: 44px;
}
.decorate .view em {
  display: block;
  width: 22px;
  height: 22px;
  overflow: hidden;
  float: left;
}
.decorate .view p {
  height: 22px;
  clear: both;
}
.decorate .view.l {
  right: auto;
  left: -70px;
}



